<script>
const loadsh = require('lodash');

export default {
  name: 'App',
  data: function () {
    return {
      songList: []
    };
  },
  computed: {
    playingSong: {
      get: function () {
        return this.$store.state.playingMusicObj.playingSong;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayingSong', value: value });
      }
    },
    isPlay: {
      get: function () {
        return this.$store.state.playingMusicObj.playingSong.isPlay;
      },
      set: function (value) {
        this.$store.commit({ type: 'setIsPlay', value: value });
      }
    },
    titleContent: function () {
      let content;
      switch (true) {
        case this.modeIndex == 1: content = '单曲循环'; break;
        case this.modeIndex == 2: content = '列表循环'; break;
        case this.modeIndex == 3: content = '随机'; break;
      }
      return content;
    },
    // 正在播放的歌单
    playSongList: {
      get: function () {
        return this.$store.state.playingMusicObj.songList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setSongList', value: value });
      }
    },
    isShowPlayingList: {
      get: function () {
        return this.$store.state.isShowPlayingList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setIsShowPlayingList', value: value });
      }
    },
    isShowContr: {
      get: function () {
        return this.$store.state.isShowContr;
      },
      set: function (value) {
        this.$store.commit({ type: 'setIsShowContr', value: value });
      }
    },
    modeIndex: {
      get: function () {
        return this.$store.state.modeIndex;
      },
      set: function (value) {
        this.$store.commit({ type: 'setModeIndex', value: value });
      }
    },
    _songList: {
      get: function () {
        return this.$store.state.playingMusicObj.songList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setSongList', value: value });
      }
    }
  },
  watch: {
    modeIndex: function (newValue) {
      if (newValue == 3) {
        this._songList.sort(() => {
          return Math.random() - Math.random();
        });
      }
    }
  },
  methods: {
    isPlayingClick: function () {
      this.isPlay = !this.isPlay;
      let audio = document.querySelector('#app>.musicAudio');
      if (audio) {
        if (this.isPlay) {
          audio.play();
        } else {
          audio.pause();
        }
      }
    },
    audioTimeupdate: function () {
      let audio = document.querySelector('#app>.musicAudio');

      let copyPlayingSong = loadsh.cloneDeep(this.playingSong);
      copyPlayingSong.playing = audio.currentTime;
      this.playingSong = copyPlayingSong;
      localStorage.setItem('playingSong', JSON.stringify(this.playingSong));
    },
    playingListClick: function () {
      this.isShowPlayingList = false;
    },
    spanClick: function () {
      if (this.modeIndex < 3) {
        ++this.modeIndex;
      } else {
        this.modeIndex = 1;
      }
    },
    svgClick: function (index) {
      let audio = document.querySelector('#app>audio');
      if (audio) {
        if (this.playSongList[index].id == this.playingSong.id) {
          audio.pause();
          this.playingSong.isPlay = false;
        }
      }
      this.playSongList.splice(index, 1);
    },
    liClick: function (song) {
      this.playSongList.forEach((v) => {
        if (v.id == song.id) {
          v.isPlay = true;
          this.playingSong = v;
        } else {
          v.isPlay = false;
        }
      });
      let audio = document.querySelector('#app>audio');
      if (audio) {
        setTimeout(() => {
          audio.play();
        }, 500);
      }
    },
    songNameClick: function () {
      this.$router.push({ path: '/palyPage' });
    },
    backconClick: function (playingSong) {
      let newSongIndex;
      this._songList.forEach((v, i) => {
        if (playingSong.id == v.id) {
          newSongIndex = i;
        }
      });
      let backIndex;
      ++newSongIndex;
      if (newSongIndex >= 0 && newSongIndex <= this._songList.length - 1) {
        backIndex = newSongIndex;
      } else {
        backIndex = 0;
      }

      let backSong = loadsh.cloneDeep(this._songList[backIndex]);
      backSong.isPlay = true;
      this.playingSong = backSong;

      let copySongList = loadsh.cloneDeep(this._songList);
      copySongList.forEach((v) => {
        if (v.id == this.playingSong.id) {
          v.isPlay = true;
        } else {
          v.isPlay = false;
        }
      });

      this._songList = copySongList;

      let audio = document.querySelector('#app>audio');
      setTimeout(() => {
        audio.play();
      }, 1000);
    },
    modeCtrl: function () {
      let audio = document.querySelector('#app>.musicAudio');
      if (this.modeIndex == 1) {
        setTimeout(() => {
          audio.play();
        }, 500);
      }
      if (this.modeIndex == 2 || this.modeIndex == 3) {
        this.backconClick(this.playingSong);
      }
    }
  }
};
</script>

