<template>
  <div class="recommend">
    <!-- 推荐编辑 -->
    <section class="bianji">
      <header>
        <span>编辑推荐</span>
      </header>
      <div class="content">
        <router-link v-for="(item) in RecommendSongs" :key="item.id" tag="div" :to="'/songList?id='+item.id"
          class="item">
          <div class="top">
            <span><img src="../../../../../public/img/24gl-headset.png">{{item.playCount}}万</span>
            <img :src="item.picUrl">
          </div>
          <p class="title">{{item.name}}</p>
        </router-link>
      </div>
    </section>
    <!--最新音乐 -->
    <section class="newMusic">
      <header>
        <span>最新音乐</span>
      </header>
      <songList :songList="songList"></songList>
    </section>
  </div>
</template>

<script>
const axios = require('axios');
import songList from '../../songList.vue';
export default {
  name: 'RecommendView',
  data: function () {
    return {
      RecommendSongs: [],
      songList: []
    }
  },
  components: {
    songList
  },
  created: function () {
    axios.get('https://apis.netstart.cn/music/personalized?limit=6')
      .then((data) => {
        data.data.result.forEach((v) => {
          this.RecommendSongs.push({
            playCount: parseInt(v.playCount / 10000),
            picUrl: v.picUrl,
            name: v.name,
            id: v.id
          });
        });
        return axios.get('https://apis.netstart.cn/music/personalized/newsong', { params: { limit: 20 } });
      })
      .then(((data) => {
        data.data.result.forEach((v) => {
          let prevName = [];
          v.song.album.artists.forEach((v) => {
            prevName.push(v.name);
          });
          prevName = prevName.join('/');

          this.songList.push({
            id: v.id,
            // URL
            URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
            // 当前的播放位置(xxx秒)
            playing: 0,
            // 总播放时长(xxx秒)
            totalTime: parseInt(v.song.duration / 1000),
            // 是否播放
            isPlay: false,
            // 歌曲的图片
            coverURL: v.picUrl,
            // 歌曲的名称
            songName: v.song.name,
            // 前缀
            prevName: prevName,
            // 后缀
            backName: v.song.album.name,
            // 是否高清
            sqMusic: v.song.sqMusic
          });
        });
      }));
  }
}
</script>

