<template>
    <div class="artistView">
        <header :style="{backgroundImage: `url(${artist.cover})`}">
            <section class="content">
                <p class="artistName">
                    {{artist.name}} {{artist.transNames}}
                </p>
                <p class="rank">{{artist.rank}}</p>
                <p class="attr">
                    <span class="left">{{artist==null?"":artist.identifyTag}}</span>
                    <span class="right" v-for="(item,index) in artist.identities" :key="index">{{item}}</span>
                </p>
            </section>
        </header>

        <section class="jianJie">
            <p>歌手简介</p>
            <p class="content">{{artist.briefDesc}}</p>
        </section>

        <section class="musicList">
            <div class="loading" v-show="loadingIsShow">
                <img src="../../../../public/img/下载.gif">
            </div>
            <songList :songList="songList"></songList>
        </section>
    </div>
</template>

<script>
const axios = require('axios');
import songList from '@/components/music/songList.vue';
export default {
    name: 'ArtistView',
    data: function () {
        return {
            artist: {
                name: "",
                transNames: "",
                rank: "",
                identifyTag: "",
                identities: "",
                id: "",
                cover: "",
                briefDesc: ""
            },
            songList: [],
            loadingIsShow: false,
        };
    },
    created: function () {
        let _this = this;
        axios.get('https://apis.netstart.cn/music/artist/detail', { params: { id: this.$route.query.id } }).
            then((data) => {
                let artist = data.data.data.artist;
                _this.artist = {
                    name: artist.name,
                    transNames: artist.transNames.length == 0 ? "" : artist.transNames.join('/'),
                    rank: artist.rank == null ? "" : '歌手榜华语地区' + artist.rank.rank,
                    identifyTag: artist.identifyTag == null ? "" : artist.identifyTag.join('/'),
                    identities: artist.identities.length == 0 ? "" : artist.identities,
                    id: artist.id,
                    cover: artist.cover,
                    briefDesc: artist.briefDesc
                };

                return axios.get('https://apis.netstart.cn/music/artists', { params: { id: artist.id } });
            })
            .then((data) => {
                data.data.hotSongs.forEach((v) => {
                    let ars = v.ar;
                    let prevName = [];
                    ars.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');

                    _this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // 当前的播放位置(xxx秒)
                        playing: 0,
                        // 总播放时长(xxx秒)
                        totalTime: parseInt(v.dt / 1000),
                        // 是否播放
                        isPlay: false,
                        // 歌曲的图片
                        coverURL: v.al.picUrl,
                        // 歌曲的名称
                        songName: v.name,
                        // 前缀
                        prevName: prevName,
                        // 后缀
                        backName: v.al.name,
                        // 是否高清
                        sqMusic: v.sq
                    });
                });
            });
    },
    components: {
        songList
    }
};
</script>

<style lang="less">
.artistView {
    background-color: rgb(252, 252, 253);

    header {
        height: 310rem;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;

        >.content {
            padding: 20rem;

            >p {
                margin: 15rem 0;
            }

            position: absolute;
            bottom: 0;
            left: 0;

            .artistName {
                font-size: 20rem;
                font-weight: bold;
                color: #fff;
            }

            .rank {
                font-size: 16rem;
                color: #fff;
            }

            .attr {
                >.left {
                    color: #fff;
                }

                >.right {
                    color: gray;
                    border: 1rem solid gray;
                    border-radius: 5rem;
                    padding: 2rem;
                    margin-left: 5rem;
                }
            }
        }
    }

    >.jianJie {
        margin-top: 5rem;
        background-color: #fff;
        padding: 0 10rem;

        >p:nth-child(1) {
            font-size: 20rem;
            font-weight: bold;
            padding: 10rem 0;
        }

        >p:nth-child(2) {
            font-size: 14rem;
            color: gray;
            text-indent: 2em;
        }
    }

    >.musicList {
        >.loading {
            padding: 50rem 0;

            >img {
                display: block;
                width: 50rem;
                margin: 0 auto;
            }
        }

    }
}
</style>