<template>
    <div class="songList">
        <header
            v-bind:style="{backgroundImage:this.headerData.coverImgUrl!=undefined ?`url(${this.headerData.coverImgUrl})`:''}">
            <div class="left">
                <span class="icon">歌单</span>
                <span><img src="../../../../public/img/24gl-headset.png">{{parseInt(headerData.playCount/10000)}}
                    万</span>
                <img v-bind:src="headerData.coverImgUrl">
            </div>
            <div class="right">
                <p class="title">{{headerData.name}}</p>
                <div class="arthor">
                    <img v-bind:src="headerData.avatarUrl">
                    <p class="name">{{headerData.nickname}}</p>
                </div>
            </div>
        </header>
        <section class="songsList">
            <p class="title">歌曲列表</p>
        </section>
        <songList :songList="songList"></songList>
    </div>
</template>

<script>
const axios = require('axios');
import songList from '@/components/music/songList.vue';
export default {
    name: 'songListView',
    data: function () {
        return {
            routeURL: '/palyPage',
            fromData: null,
            headerData: {
                coverImgUrl: undefined,
                playCount: 0,
                name: undefined,
                avatarUrl: undefined,
                nickname: undefined
            },
            songList: []
        }
    },
    components: { songList },
    created: function () {
        // let _this = this;
        // 歌单详情
        // https://apis.netstart.cn/music/playlist/detail?id=24381616
        // 获取歌单的所有歌曲
        // https://apis.netstart.cn/music/playlist/track/all?id=24381616&limit=10&offset=0
        this.fromData = this.$route.query;
        axios.get('https://apis.netstart.cn/music/playlist/detail', { params: { id: this.fromData.id } })
            .then((data) => {
                this.headerData.coverImgUrl = data.data.playlist.coverImgUrl;
                this.headerData.playCount = data.data.playlist.playCount;
                this.headerData.name = data.data.playlist.name;
                this.headerData.avatarUrl = data.data.playlist.creator.avatarUrl;
                this.headerData.nickname = data.data.playlist.creator.nickname;
                return axios.get('https://apis.netstart.cn/music/playlist/track/all', {
                    params: {
                        id: this.fromData.id,
                        limit: 10,
                        offset: 0
                    }
                })
            }).then((data) => {
                data.data.songs.forEach((v) => {
                    let ars = v.ar;
                    let prevName = [];
                    ars.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');
                    this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // 当前的播放位置(xxx秒)
                        playing: 0,
                        // 总播放时长(xxx秒)
                        totalTime: parseInt(v.dt / 1000),
                        // 是否播放
                        isPlay: false,
                        // 歌曲的图片
                        coverURL: v.al.picUrl,
                        // 歌曲的名称
                        songName: v.name,
                        // 前缀
                        prevName: prevName,
                        // 后缀
                        backName: v.al.name,
                        // 是否高清
                        sqMusic: v.sq
                    });
                });
            });
    }

}
</script>

<style lang="less">
.songList {
    >header {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 20rem 0;
        // filter: blur(100px);

        >.left {
            width: 126rem;
            position: relative;

            >img {
                display: block;
                width: 100%;
            }

            >.icon {
                position: absolute;
                top: 10rem;
                left: 0;
                padding: 0.5rem 6rem;
                background-color: red;
                color: #fff;
                border-radius: 0 35% 35% 0;
            }

            >span:nth-child(2) {
                display: flex;
                position: absolute;
                top: 0;
                right: 0;
                color: #fff;

                >img {
                    display: block;
                    width: 15rem;
                }
            }
        }

        >.right {
            width: 50%;

            >.title {
                font-size: 18rem;
                margin-bottom: 10rem;
                color: #fff;
            }

            >.arthor {
                display: flex;
                align-items: center;

                >img {
                    display: block;
                    width: 30rem;
                    border-radius: 50%;
                    margin-right: 10rem;
                }

                >.name {
                    font-size: 14rem;
                    font-weight: 500;
                    color: gray;
                }
            }
        }
    }

    >.songsList {
        .title {
            background-color: #eeeff0;
            padding: 5rem;
        }
    }
}
</style>