import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 目前正在播放的音乐的信息
    playingMusicObj: {
      // 正在播放的歌单
      songList: JSON.parse(localStorage.getItem('songList')) || [],
      // 正在播放的歌曲
      playingSong: JSON.parse(localStorage.getItem('playingSong')) ? JSON.parse(localStorage.getItem('playingSong')) : {
        id: undefined,
        // URL
        URL: undefined,
        // 当前的播放位置(xxx秒)
        playing: 0,
        // 总播放时长(xxx秒)
        totalTime: undefined,
        // 是否播放
        isPlay: false,
        // 歌曲的图片
        coverURL: undefined,
        // 歌曲的名称
        songName: undefined,
        // 前缀
        prevName: undefined,
        // 后缀
        backName: undefined,
        // 是否高清
        sqMusic: null
      }
    },
    isShowPlayingList: false,
    isShowContr: localStorage.getItem('isShowContr') ? localStorage.getItem('isShowContr') : false,
    // 1:单曲循环 2.列表循环 3.随机
    modeIndex: 1,
    searchHistory: JSON.parse(localStorage.getItem('searchHistory')) || []
  },
  getters: {

  },
  mutations: {
    setPlayingSong: function (state, param) {
      state.playingMusicObj.playingSong = param.value;
    },
    setIsPlay: function (state, param) {
      state.playingMusicObj.playingSong.isPlay = param.value;
    },
    setSongList: function (state, param) {
      state.playingMusicObj.songList = param.value;
    },
    setPlaying: function (state, param) {
      state.playingMusicObj.playingSong.playing = param.value;
    },
    setIsShowPlayingList: function (state, param) {
      state.isShowPlayingList = param.value;
    },
    setIsShowContr: function (state, param) {
      state.isShowContr = param.value;
    },
    setModeIndex: function (state, param) {
      state.modeIndex = param.value;
    },
    setSearchHistory: function (state, param) {
      state.searchHistory = param.value;
    }
  },
  actions: {
  },
  modules: {
  }
})
