import Vue from 'vue';
import VueRouter from 'vue-router';

import HomeView from '@/views/music/Home/HomeView.vue';
import RecommendView from '@/components/music/HomePage/childPage/RecommendView.vue';
import HotMusicView from '@/components/music/HomePage/childPage/HotMusicView.vue';
import SearchView from '@/components/music/HomePage/childPage/SearchView.vue';
import songListView from '@/views/music/songList/songListView.vue';
import ArtistView from '@/views/music/Artist/ArtistView.vue';

import SongplayComponent from '../components/music/SongPlay/SongplayComponent.vue';
import IyricComponent from '../components/music/SongPlay/IyricComponent.vue';
import songPlayView from '../views/music/songPlay/songPlayView.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    components: {
      appPage: HomeView
    },
    children: [
      {
        path: '/',
        components: { tagPage: RecommendView }
      },
      {
        path: 'hot',
        components: { tagPage: HotMusicView }
      },
      {
        path: 'search',
        components: { tagPage: SearchView }
      }
    ]
  },
  {
    path: '/songList',
    components: {
      appPage: songListView
    }
  },
  {
    path: '/artist',
    components: {
      appPage: ArtistView
    }
  },
  {
    path: '/palyPage',
    components: {
      appPage: songPlayView
    },
    children: [
      {
        path: '/palyPage',
        components: {
          songAndlyric: SongplayComponent
        }
      },
      {
        path: '/palyPage/Iyric',
        components: {
          songAndlyric: IyricComponent
        }
      }
    ]
  }
];

const router = new VueRouter({
  routes
});

export default router;
