<template>
  <div class="Hot">
    <header>
      <img src="../../../../../public/img/热歌榜.png">
      <p> 更新日期:{{date.MM<10?'0'+date.MM:date.MM}}月{{date.DD<10?'0'+date.DD:date.DD}}日 </p>
    </header>
    <div class="content">
      <div class="loading" v-show="loadingIsShow">
        <img src="../../../../../public/img/下载.gif">
      </div>
      <songList :songList="songList"></songList>
    </div>
  </div>
</template>

<script>
const axios = require('axios');
import songList from '../../songList.vue';

export default {
  name: 'HotMusicView',
  data: function () {
    return {
      loadingIsShow: false,
      songList: [],
      date: {
        MM: new Date().getMonth() + 1,
        DD: new Date().getDate()
      }
    }
  },
  components: {
    songList
  },
  created: function () {
    let _this = this;
    _this.loadingIsShow = true;
    axios.get('./data/hotMusic.json').then((data) => {
      _this.loadingIsShow = false;
      data.data.playlist.tracks.forEach((v) => {
        let prevName = [];

        v.ar.forEach((v) => {
          prevName.push(v.name);
        });
        prevName = prevName.join('/');
        this.songList.push({
          id: v.id,
          // URL
          URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
          // 当前的播放位置(xxx秒)
          playing: 0,
          // 总播放时长(xxx秒)
          totalTime: parseInt(v.dt / 1000),
          // 是否播放
          isPlay: false,
          // 歌曲的图片
          coverURL: v.al.picUrl,
          // 歌曲的名称
          songName: v.name,
          // 前缀
          prevName: prevName,
          // 后缀
          backName: v.al.name,
          // 是否高清
          sqMusic: v.sq
        });
      });
    });
  }
}
</script>

<style lang="less">
.Hot {
  >header {
    background-image: url('../../../../../public/img/hot_music_bg_2x.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 25rem;

    >img {
      width: 40%;
      margin-bottom: 5rem;
    }

    >p {
      color: #fff;
      font-size: 14rem;
      font-weight: bold;
    }
  }

  >.content {
    >.loading {
      padding: 50rem 0;

      >img {
        display: block;
        width: 50rem;
        margin: 0 auto;
      }
    }
  }
}
</style>