<template>
    <div class="lyricComponent" v-on:click="lyricComponentClick">
        <div class="content">
            <ul class="lyrics" :style="{top:lyricTop+'vh'}">
                <li v-for="(item,index) in lyric" v-bind:key="index">
                    <span class="lyric">
                        <span :class="{cover:true, anim:isActive(index,lyric,playingSong.playing)&&playingSong.isPlay}"
                            :style="{animationDuration:getLyDuration(index)+'s'}">{{item.lyric}}</span>
                        <span class="buttom">{{item.lyric}}</span>
                    </span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
const axios = require('axios');
export default {
    name: 'IyricComponent',
    data: function () {
        return {
            lyric: [],
            // 当前高亮的歌词的索引
            lightIndex: 0,
            // 当前歌曲的偏移量
            lyricTop: 200
        }
    },
    methods: {
        lyricComponentClick: function () {
            this.$router.go(-1);
        },
        /**
         *  判断当前遍历到的歌词是否点亮
         * （arr）数组
         * （Index）当前遍历到的元素的索引
         * （playing）歌曲目前的播放位置（秒数）
        */
        isActive(index, arr, playing) {
            if (index == arr.length - 1) {
                if (playing >= arr[index].time) {
                    this.lightIndex = index;
                }
                return playing >= arr[index].time;
            } else {
                let nextIndex = index + 1;
                if (playing >= arr[index].time && playing < arr[nextIndex].time) {
                    this.lightIndex = index;
                }
                return playing >= arr[index].time && playing < arr[nextIndex].time;
            }

        },
        /**
         * 获取当前歌词的播放时长
         * */
        getLyDuration(index) {
            if (index != this.lyric.length - 1) {
                let nextIndex = index + 1;
                return this.lyric[nextIndex].time - this.lyric[index].time;
            } else {
                return this.playingSong.totalTime - this.lyric[index].time;
            }
        }
    },
    computed: {
        playingSong: {
            get: function () {
                return this.$store.state.playingMusicObj.playingSong;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingSong', value: value });
            }
        },
    },
    created: function () {
        // https://apis.netstart.cn/music/lyric
        axios.get('https://apis.netstart.cn/music/lyric', { params: { id: this.playingSong.id } }).then((data) => {
            data.data.lrc.lyric.split(/\n/g).forEach((v) => {
                let indexOfL = v.indexOf('[');
                let indexOfR = v.indexOf(']');
                // 歌词对应的时间
                let time = v.slice(indexOfL + 1, indexOfR);
                let dotIndex = time.indexOf('.');
                time = time.slice(0, dotIndex);
                let mm = parseInt(time.slice(0, 2));
                let ss = parseInt(time.slice(3));

                let lyric = v.slice(indexOfR + 1);
                this.lyric.push({ time: mm * 60 + ss, lyric: lyric });
            });
        });
    },
    watch: {
        /**
         * 深度检测
         * （1）Vue中的watch默认不能检测对象内部的属性的变化。
         * （2）配置deep:true可以检测对象内部值的变化。
         * 
         * immediate：初始化时让handler调用一次。默认值为false。
         * handler：当检测的值发生变化时调用。
         * */
        "playingSong.playing": {
            immediate: false,
            handler(newValue) {
                this.playingSong.playing = newValue;
            }
        },
        'lightIndex': {
            handler(newValue) {
                this.lyricTop = 35 - newValue * 2.5;
            }
        }
    }
}
</script>

<style lang="less">
.lyricComponent {
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;

    >.content {
        height: 90%;
        overflow: hidden;
        position: relative;

        >.lyrics {
            width: 100%;
            position: absolute;
            top: 100px;

            @keyframes lyricTran {
                0% {
                    width: 0;
                }

                100% {
                    width: 100%;
                }
            }

            li {
                text-align: center;
                color: rgba(255, 255, 255, 0.486);
                font-size: 14rem;

                .lyric {
                    position: relative;

                    >.cover {
                        width: 0;
                        position: absolute;
                        top: 0;
                        white-space: nowrap;
                        color: #fff;
                        overflow: hidden;
                        z-index: 1;
                    }

                    >.buttom,
                    >.cover {
                        height: 16rem;
                        line-height: $height;
                    }

                    /* 动画 */
                    >.anim {
                        animation: lyricTran;
                        animation-timing-function: ease;
                        animation-iteration-count: 1;
                    }
                }
            }
        }
    }
}
</style>