<template>
    <div class="SongplayComponent">
        <router-link :to="'/palyPage/Iyric?id='+playingSong.id" tag="div">
            <!-- 唱片吊针 -->
            <img src="../../../../public/img/needle-ab.png"
                v-bind:class="{headSet:true,noodelRun:playingSong.isPlay,noodelPau:!playingSong.isPlay}">
            <div :class="{cilcle:true, roundAni:true,running:playingSong.isPlay,paused:!playingSong.isPlay}">
                <div class="round"></div>
                <img :src="playingSong.coverURL">
            </div>
        </router-link>
    </div>
</template>

<script>
const axios = require('axios');

export default {
    name: 'SongplayComponent',
    data: function () {
        return {
            pageData: {
                imgUrl: ''
            }
        }
    },
    computed: {
        playingSong: {
            get: function () {
                return this.$store.state.playingMusicObj.playingSong;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingSong', value: value });
            }
        },
    },
    created: function () {
        axios.get(`https://apis.netstart.cn/music/song/detail?ids=${this.playingSong.id}`).then(
            (data) => {
                this.pageData.imgUrl = data.data.songs[0].al.picUrl;
            }
        );
    }
}
</script>

<style lang="less">
// 唱片旋转动画
@keyframes roundAni {
    0% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(360deg);
    }
}

// 唱片指针动画(开始)
@keyframes noodelAniRun {
    0% {
        transform: rotateZ(315deg);
    }

    100% {
        transform: rotateZ(360deg);
    }
}

// 唱片指针动画(暂停)
@keyframes noodelAniPau {
    0% {
        transform: rotateZ(360deg);
    }

    100% {
        transform: rotateZ(315deg);
    }
}

.SongplayComponent {
    overflow: hidden;
    height: 80vh;

    >div {
        position: relative;
        height: 100%;

        // 旋转动画
        .roundAni {
            animation: roundAni;
            animation-duration: 10s;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
        }

        // 动画播放
        .running {
            animation-play-state: running;
        }

        // 动画暂停
        .paused {
            animation-play-state: paused;
        }

        .headSet {
            display: block;
            width: 120rem;
            position: absolute;
            top: 0rem;
            z-index: 1;
            left: 40%;
            transform-origin: 24rem 24rem;
            transform: rotateZ(315deg);
        }

        .noodelRun {
            animation: noodelAniRun;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-duration: 1s;
            transform: rotateZ(360deg);
        }

        .noodelPau {
            animation: noodelAniPau;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-duration: 1s;
            transform: rotateZ(315deg);
        }

        .cilcle {
            width: 80vw;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            top: 120rem;
            left: calc(50% - 40vw);

            .round {
                width: 100%;
                height: 80vw;
                background-image: url('../../../../public/img/黑色圆盘.png');
                background-repeat: no-repeat;
                background-size: cover;
            }

            >img {
                position: absolute;
                display: block;
                width: 65%;
                border-radius: 50%;
            }
        }
    }
}
</style>